import { Component } from '@angular/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  start = false;
  mode = { key: 'plus', text: 'Plus' };
  maxLevel = 1;
  level = 1;
  a = 0;
  b = 0;
  result = '';
  decimal = false;
  point = 0;
  gameTime = 15;
  countDownTime = this.gameTime;
  plusTime = Math.ceil(this.countDownTime / 15) + 1;
  interval: any;

  constructor() { }

  changeMode(key: any) {
    if (key === 'plus') {
      this.mode = { key: 'plus', text: 'Plus (+)' };
    } else if (key === 'minus') {
      this.mode = { key: 'minus', text: 'Minus (-)' };
    } else if (key === 'multi') {
      this.mode = { key: 'multi', text: 'Multi (x)' };
    } else if (key === 'div') {
      this.mode = { key: 'div', text: 'Div (:)' };
    } else if (key === 'plus_minus') {
      this.mode = { key: 'plus_minus', text: 'Plus, Minus (+ , -)' };
    } else if (key === 'untimate') {
      this.mode = { key: 'untimate', text: 'Ultimate (+, -, x, :)' };
    }
  }

  startGame() {
    if (this.start === false) {
      this.start = true;
    }

    this.level = 1;
    this.a = 0;
    this.b = 0;
    this.point = 0;
    this.countDownTime = this.gameTime;
    this.clearResult();
    this.startCountdown();
    this.calculation();
  }

  calculation() {
    if (this.mode.key === 'plus') {
      this.plus();
      if (this.countDownTime > 30) {
        this.countDownTime = 30;
      }
    }
  }

  plus() {
    const critLevel = (this.level + 1) * 5;
    this.a = this.randomNumber(critLevel);
    this.b = this.randomNumber(critLevel);
    if (this.a === 0 || this.b === 0 || this.a + this.b < critLevel - 1) {
      this.plus();
    }
  }

  chooseNumber(key: any) {
    if (key === '-') {
      if (this.result === '') {
        this.result += key;
      }
    } else if (key === '.') {
      if (this.decimal === false && this.result !== '') {
        this.result += key;
        this.decimal = true;
      }
    } else {
      this.result += key;
    }
  }

  clearResult() {
    this.result = '';
    this.decimal = false;
  }

  uplevel() {
    this.level++;
    if (this.maxLevel <= this.level) {
      this.maxLevel = this.level;
    }
  }

  checkResult() {
    const sum = this.a + this.b;
    if (Number(this.result) === sum) {
      this.countDownTime += this.plusTime;
      this.clearResult();
      this.calculation();
      this.uplevel();
    } else {
      this.clearResult();
    }
  }

  randomNumber(size: any) {
    return Math.floor(Math.random() * size);
  }

  startCountdown() {
    this.interval = setInterval(() => {
      this.countDownTime--;
      if (this.countDownTime < 0) {
        clearInterval(this.interval);
        this.start = false;
        this.countDownTime = this.gameTime;
      }
    }, 1000);
  }
}
